<template>
  <view class="edit-profile-page">
    <!-- ÂØºËà™Ê†è -->
    <view class="navbar">
      <view class="navbar-left" @click="goBack">
        <uni-icons type="left" size="24" color="#333"></uni-icons>
      </view>
      <text class="navbar-title">ÁºñËæëËµÑÊñô</text>
      <view class="navbar-right" @click="saveProfile">
        <text class="save-text">‰øùÂ≠ò</text>
      </view>
    </view>
    
    <!-- ÁºñËæëË°®Âçï -->
    <scroll-view class="form-container" scroll-y>
      <!-- Â§¥ÂÉè -->
      <view class="form-section">
        <view class="form-item avatar-item">
          <text class="form-label">Â§¥ÂÉè</text>
          <view class="avatar-upload" @click="changeAvatar">
            <image 
              :src="editForm.avatarUrl || '/static/default-avatar.png'" 
              class="upload-avatar"
              mode="aspectFill"
            ></image>
            <uni-icons type="camera" size="24" color="#fff" class="camera-icon"></uni-icons>
          </view>
        </view>
      </view>
      
      <!-- Âü∫Êú¨‰ø°ÊÅØ -->
      <view class="form-section">
        <view class="form-item">
          <text class="form-label">ÊòµÁß∞</text>
          <input 
            v-model="editForm.nickname" 
            class="form-input"
            placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞"
            maxlength="20"
          />
        </view>
        
        <view class="form-item">
          <text class="form-label">ÊÄßÂà´</text>
          <view class="gender-selector">
            <view 
              class="gender-option"
              :class="{ active: editForm.user_sex === 'Áî∑' }"
              @click="editForm.user_sex = 'Áî∑'"
            >
              <text>Áî∑</text>
            </view>
            <view 
              class="gender-option"
              :class="{ active: editForm.user_sex === 'Â•≥' }"
              @click="editForm.user_sex = 'Â•≥'"
            >
              <text>Â•≥</text>
            </view>
            <view
              class="gender-option"
              :class="{ active: editForm.user_sex === '‰øùÂØÜ' }"
              @click="editForm.user_sex = '‰øùÂØÜ'"
            >
              <text>‰øùÂØÜ</text>
            </view>
          </view>
        </view>
        
        <!-- üî• ‰øÆÊîπÔºöÂ≠¶Èô¢Êîπ‰∏∫Âè™ËØªÊòæÁ§∫ -->
        <view class="form-item">
          <text class="form-label">Â≠¶Èô¢</text>
          <view class="form-readonly">
            <text class="readonly-text">{{ editForm.major || 'ÊöÇÊó†' }}</text>
            <text class="readonly-tip">Ê≠§‰ø°ÊÅØ‰∏çÂèØ‰øÆÊîπ</text>
          </view>
        </view>
      </view>
      
      <!-- ‰∏™‰∫∫ÁÆÄ‰ªã -->
      <view class="form-section">
        <view class="form-item">
          <text class="form-label">‰∏™‰∫∫ÁÆÄ‰ªã</text>
          <textarea 
            v-model="editForm.user_introduce" 
            class="form-textarea"
            placeholder="ËØ∑ËæìÂÖ•‰∏™‰∫∫ÁÆÄ‰ªã"
            maxlength="200"
            :show-count="true"
          ></textarea>
        </view>
      </view>
      
      <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
      <view class="form-tips">
        <text class="tips-text">‚Ä¢ Â§¥ÂÉèÂíåÊòµÁß∞ÊòØÊÇ®Âú®Á§æÂå∫ÁöÑÂ±ïÁ§∫ÂêçÁâá</text>
        <text class="tips-text">‚Ä¢ Â≠¶Èô¢‰ø°ÊÅØÁî±Á≥ªÁªüËÆ§ËØÅÔºå‰∏çÂèØ‰øÆÊîπ</text>
        <text class="tips-text">‚Ä¢ ÁúüÂÆûÁöÑ‰∏™‰∫∫‰ø°ÊÅØÊúâÂä©‰∫éÂª∫Á´ã‰ø°‰ªª</text>
      </view>
    </scroll-view>
  </view>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'EditProfile',
  data() {
    return {
      editForm: {
        nickname: '',
        avatarUrl: '',
        user_sex: '',
        major: '', // üî• Âè™Áî®‰∫éÊòæÁ§∫Ôºå‰∏ç‰ºöË¢´‰øÆÊîπ
        user_introduce: ''
      },
      uploading: false,
      hasChanges: false,
	  enableWatch: false  // üî• Êñ∞Â¢û
    }
  },
  
  computed: {
    ...mapState('m_user', ['userBase', 'openid', 'userMajor'])
  },
  
  onLoad() {
    this.initEditForm()
	// üî• Âª∂ËøüÂêØÁî®ÁõëÂê¨
	    this.$nextTick(() => {
	      this.enableWatch = true
	    })
  },
  
  // ÁõëÂê¨Ë°®ÂçïÂèòÂåñ
  watch: {
    editForm: {
      handler() {
         // üî• ‰øÆÂ§çÔºöÂè™ÊúâÂêØÁî®ÁõëÂê¨ÂêéÊâçÊ†áËÆ∞
               if (this.enableWatch) {
                 this.hasChanges = true
               }
      },
      deep: true
    }
  },
  
  // ËøîÂõûÂâçÊèêÁ§∫
  onBackPress() {
    if (this.hasChanges) {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'ÊÇ®ÊúâÊú™‰øùÂ≠òÁöÑ‰øÆÊîπÔºåÁ°ÆÂÆöË¶ÅÁ¶ªÂºÄÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            uni.navigateBack()
          }
        }
      })
      return true // ÈòªÊ≠¢ÈªòËÆ§ËøîÂõû
    }
  },
  
  methods: {
    ...mapMutations('m_user', ['updateUserBase']),
    
    // ÂàùÂßãÂåñË°®Âçï
    initEditForm() {
      this.editForm = {
        nickname: this.userBase.nickname || '',
        avatarUrl: this.userBase.avatarUrl || '',
        user_sex: this.userBase.user_sex || '‰øùÂØÜ',
        major: this.userBase.major || '', // üî• Âè™ËØªÂ≠óÊÆµ
        user_introduce: this.userBase.user_introduce || ''
      }
      this.hasChanges = false
	  this.enableWatch = false  // üî• ‰øùÂ≠òÂêéÁ¶ÅÁî®ÁõëÂê¨ÔºåÈÅøÂÖçÂØºËà™ËøîÂõûÊó∂ËØØÂà§
    },
    
    // ËøîÂõû
    goBack() {
      if (this.hasChanges) {
        uni.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÊÇ®ÊúâÊú™‰øùÂ≠òÁöÑ‰øÆÊîπÔºåÁ°ÆÂÆöË¶ÅÁ¶ªÂºÄÂêóÔºü',
          success: (res) => {
            if (res.confirm) {
              uni.navigateBack()
            }
          }
        })
      } else {
        uni.navigateBack()
      }
    },
    
    // ‰øùÂ≠òËµÑÊñô
    async saveProfile() {
      if (!this.editForm.nickname.trim()) {
        return uni.showToast({ 
          title: 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫', 
          icon: 'none' 
        })
      }

      try {
        uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...', mask: true })
        
        // üî• ‰øÆÊîπÔºö‰∏çÂèëÈÄÅ major Â≠óÊÆµÂà∞ÂêéÁ´Ø
        const { data: res } = await uni.$http.post('/users/updateProfile', {
          openid: this.userBase.openid,
          nickname: this.editForm.nickname.trim(),
          avatarUrl: this.editForm.avatarUrl,
          user_sex: this.editForm.user_sex,
          major: this.editForm.major.trim(),
          user_introduce: this.editForm.user_introduce.trim()
        })

        if (res.meta.status === 200) {
          // Êõ¥Êñ∞ Vuex
          this.updateUserBase({
            ...this.userBase,
            nickname: this.editForm.nickname.trim(),
            avatarUrl: this.editForm.avatarUrl,
            user_sex: this.editForm.user_sex,
            major: this.editForm.major,
            user_introduce: this.editForm.user_introduce.trim()
          })

          this.hasChanges = false
          
          uni.showToast({ 
            title: '‰øùÂ≠òÊàêÂäü', 
            icon: 'success',
            duration: 1500
          })
          
          setTimeout(() => {
            uni.navigateBack()
          }, 1500)
        } else {
          throw new Error(res.meta.msg || '‰øùÂ≠òÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('‰øùÂ≠òËµÑÊñôÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: error.message || '‰øùÂ≠òÂ§±Ë¥•', 
          icon: 'none' 
        })
      } finally {
        uni.hideLoading()
      }
    },
    
    // Êõ¥Êç¢Â§¥ÂÉè
    changeAvatar() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: async (res) => {
          const tempFilePath = res.tempFilePaths[0]
          const uploadedUrls = await this.uploadImages([tempFilePath])
          
          if (uploadedUrls && uploadedUrls.length > 0) {
            this.editForm.avatarUrl = uploadedUrls[0]
            uni.showToast({ 
              title: 'Â§¥ÂÉèÂ∑≤Êõ¥Êñ∞', 
              icon: 'success' 
            })
          }
        },
        fail: (err) => {
          console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•:', err)
          uni.showToast({
            title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
            icon: 'none'
          })
        }
      })
    },
    
    // ‰∏ä‰º†ÂõæÁâá
    async uploadImages(filePaths) {
      if (!filePaths || filePaths.length === 0) {
        return []
      }

      this.uploading = true
      uni.showLoading({
        title: '‰∏ä‰º†‰∏≠...',
        mask: true
      })

      const imageUrls = []

      try {
        for (let i = 0; i < filePaths.length; i++) {
          const filePath = filePaths[i]

          // Ëé∑Âèñ‰∏ä‰º†Âá≠ËØÅ
          const { data: tokenRes } = await uni.$http.get('/upload/token', {
            openid: this.openid,
            fileType: 'image'
          })

          if (tokenRes.meta.status !== 200) {
            throw new Error('Ëé∑Âèñ‰∏ä‰º†Âá≠ËØÅÂ§±Ë¥•')
          }

          // ËØªÂèñÊñá‰ª∂ÂÜÖÂÆπ
          const fileContent = await new Promise((resolve, reject) => {
            uni.getFileSystemManager().readFile({
              filePath: filePath,
              encoding: 'base64',
              success: (res) => resolve(res.data),
              fail: reject
            })
          })

          // ‰∏ä‰º†Âà∞‰∫ëÂ≠òÂÇ®
          await new Promise((resolve, reject) => {
            uni.request({
              url: tokenRes.message.publicUrl,
              method: 'PUT',
              header: {
                'Content-Type': 'application/octet-stream'
              },
              data: uni.base64ToArrayBuffer(fileContent),
              success: (res) => {
                if (res.statusCode === 200) {
                  resolve(res)
                } else {
                  reject(new Error(`‰∏ä‰º†Â§±Ë¥•: ${res.statusCode}`))
                }
              },
              fail: reject
            })
          })

          imageUrls.push(tokenRes.message.publicUrl)
        }

        uni.hideLoading()
        return imageUrls

      } catch (error) {
        console.error('[ERROR] ‰∏ä‰º†Â§±Ë¥•:', error)
        uni.hideLoading()
        uni.showToast({
          title: error.message || '‰∏ä‰º†Â§±Ë¥•',
          icon: 'none',
          duration: 2000
        })
        return []
      } finally {
        this.uploading = false
      }
    }
  }
}
</script>

